  border-radius: 12px;
}
.border-transparent{
  border: 1px solid transparent;
}

.border-gray{
  border-top: 1px solid #C1C1C1 !important;
}

.border-bottom-green{
  position: relative;
  &::after {
      content: "";
      position: absolute;
      bottom: -16px;
      display: inline-block;
      background-color: #62DB54;
      width: 50px;
      height: 3px;
      @include screen-sm{
        left: 50%;
        transform: translate(-50%, -50%);
      }
      @include screen-lg{
        left: 0;
        transform: none;
      }
    }
}
.border-bottom-green-center{
  position: relative;
  &::after {
      content: "";
      position: absolute;
      bottom: -16px;
