//*手把手帶你
// 因為 large 和 small 的作用範圍不同，所以只在 small 時有此 border hover 效果，large 要消失
.hover-color-sm-gradient{
  opacity: .5;
  color: #fff;
  border: 1px solid transparent;
  border-radius: 12px;
  @include screen-sm {
    &:hover{
      border: 1px solid #62DB54;
      transition: border .5s ease-in-out;
      opacity: 1;
      background: #000;
      .color-gradient {
        background: rgb(98, 219, 84);
        background: linear-gradient(0deg, rgba(98, 219, 84, 1) 0%, rgba(9, 172, 245, 1) 100%);
        background-clip: text;
        -webkit-background-clip: text;
        color: transparent;
      }
    }
  }
  @include screen-lg{
    &:hover{
      border: 1px solid transparent;
      background: none;
    }
  }
}
// 只在 large 時才套用
.hover-color-lg-gradient {
  color: #fff;
  border: 1px solid transparent;
  border-radius: 12px;

  @include screen-lg {
    &:hover {
      border: 1px solid #62DB54;
      background: #000;
      transition: border .5s ease-in-out;
    }
  }
}
