}
.hover-opacity-bottom-green{
  &:hover{
    .hover-opacity-100 {
        opacity: 1;
      }//文字和 icon
    .hover-border-bottom-green {
      position: relative;
      &::after {
        content: "";
        position: absolute;
        bottom: -16px;
        left: 50%;
        display: inline-block;
        background-color: #62DB54;
        width: 50px;
        height: 3px;
        transform: translate(-50%, -50%);
      }
      @include screen-lg{
        &::after{
          left: 0;
          transform: none;
        }
      }
    }
    .hover-color-green{
      color: #62DB54;
    }//icon
  }
}//四大滿版區塊

// 四角落圓角 border
.hover-border-radius{
  &:hover{
    .top-left-radius {
      position: absolute;
      top: 0;
      left: 0;
      width: 32px;
      height: 32px;
      border-top: 2px solid #62DB54;
      border-left: 2px solid #62DB54;
      border-radius: 10px 0 0 0;
      transition: border .1s;
    }
    .top-right-radius {
      position: absolute;
      top: 0;
      right: 0;
      border-top: 2px solid #62DB54;
      border-right: 2px solid #62DB54;
      border-radius: 0 10px 0 0;
      width: 32px;
      height: 32px;
      transition: border .1s;
    }
    
    .bottom-left-radius {
      position: absolute;
      bottom: 0;
      left: 0;
      border-bottom: 2px solid #62DB54;
      border-left: 2px solid #62DB54;
      border-radius: 0 0 0 10px;
      width: 32px;
      height: 32px;
      transition: border .1s;
    }
    
    .bottom-right-radius {
      position: absolute;
      bottom: 0;
      right: 0;
      border-bottom: 2px solid #62DB54;
      border-right: 2px solid #62DB54;
      border-radius: 0 0 10px 0;
      width: 32px;
      height: 32px;
      transition: border .1s;
    }
  }
}

//a 標籤擴展
.stretched-link{
  &::after{
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 2;
  }
}